.App-header {
  min-height: 15vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(15px + 2vmin);
  color: white;
}

.row {
  display: flex;
  height: 100%;
  align-self: stretch;
}

.col {
  flex: 100%;
  word-break: break-all;
  background-color: white;
}

.whole {
  margin: 5px;
  background-color: #282c34;
}

.additional-info {
  color: white; 
  font-size: calc(1px + 2vmin);
  align-content: start;
  margin: 10px; 
}

.footer {
  position: fixed; 
  padding: 15px; 
  background-color: #282c34;
  width: 100%;
  color: white; 
}

html {
  min-height: 100%; 
  height: 100vh;
}